body {
	background-color: #F5F5F5;
	width: 100%;
	display: flex;
	justify-content: center;
}
.container {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0px 20px; }
  .container #header {
    font-family: 'Josefin Sans', sans-serif;
    font-style: italic;
    color: #333333; }
  .container #text {
    font-family: 'Josefin Sans', sans-serif;
    color: #333333; }
  .container #link-item1:hover {
    background-color: red;
    padding: 0px 0px; }
  .container #link-item2:hover {
    background-color: orange;
    padding: 0px 0px; }
  .container #link-item3:hover {
    background-color: yellow;
    padding: 0px 0px; }
  .container .link-list {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around; }
  .container .list-item {
    margin: 10px 0px; }
  .container .banner {
    align-self: center; }
  .container .information {
    display: flex;
    flex-direction: column;
    margin: 20px;
    width: 100%;
    height: 100%; }
    .container .information .background-container {
      border-bottom: 1px solid #333333; }
      .container .information .background-container .background-content {
        display: flex;
        justify-content: space-around;
        margin: 50px 170px; }
        .container .information .background-container .background-content .profile-image {
          width: 180px;
          height: 180px;
          border-radius: 100%; }

.project-card {
  width: 750px; }

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.projects-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center; }
  .projects-container .projects-item {
    margin: 20px auto; }

.container {
  height: 100%;
  margin-top: 5px; }
  .container #icon {
    color: #333333;
    font-size: 100px; }
  .container .icons {
    margin: 50px 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .container .icons .icon-sector {
      margin: 20px 50px;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .container .icons .icon-sector .icon-text {
        padding-top: 5px; }

